<template>
    <div class="wrap">
        <div class="nav">
            <div :class="item.style" v-for="(item,index) in nav" :key="index" @click="changenav(index, item)">{{
                    item.name
            }}</div>
        </div>
        <div class="table">
            <el-table :data="tableData" style="width: 100%" highlight-current-row="true">
                <el-table-column type="selection" width="100">
                </el-table-column>
                <el-table-column width="140" align="center" label="图片">
                    <template slot-scope="scope">
                        <div class="tu">
                            <img :src="require('@/assets/img' + scope.row.pic)" style="width:100%;height:100%;" />
                        </div>
                    </template>
                </el-table-column>
                <el-table-column prop="cate" label="广告类型" align="center">
                </el-table-column>
                <el-table-column prop="time" label="投放时间" align="center">
                </el-table-column>
                <el-table-column label="操作" align="center" width="300">
                    <template slot-scope="scope">
                        <div class="operate">
                            <el-button type="text" >编辑</el-button>
                            <el-button type="text" @click.stop="deletes(scope.row)">删除</el-button>
                            <el-button type="text" @click.stop="takedowns(scope.row)">下架</el-button>
                        </div>
                    </template>
                </el-table-column>
            </el-table>
            <div class="footer">
                <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
                    :current-page.sync="currentPage1" :page-size="4" layout="total, prev, pager, next"
                    :total="this.tableData.length">
                </el-pagination>
            </div>
        </div>
    </div>
</template>
<script>
import { pictureAdg } from '@/request/api'
export default {
    beforeCreate() {
        pictureAdg().then(res => {
            this.netData = res
            this.tableData = res.filter(e => e.nav == this.navflag)
        })
    },
    data() {
        return {
            nav: [
                {
                    name: "个人主页",
                    style: 'act'
                },
                {
                    name: "项目推广首页",
                    style: 'li'
                },
                {
                    name: "商品推广",
                    style: 'li'
                },
                {
                    name: "项目推广",
                    style: 'li'
                },
                {
                    name: "名片推广",
                    style: 'li'
                },
                {
                    name: "快购推广",
                    style: 'li'
                },
                {
                    name: "拍吧推广",
                    style: 'li'
                },
                {
                    name: "券式团购推广",
                    style: 'li'
                },
                {
                    name: "新分享推广",
                    style: 'li'
                },
                {
                    name: "营销活动推广",
                    style: 'li'
                },
                {
                    name: "公益推广",
                    style: 'li'
                },
                {
                    name: "商家端首页",
                    style: 'li'
                }
            ],
            navflag: "个人主页",
            netData: null,
            tableData: null,
        }
    },
    watch: {
        navflag(a, b) {
            this.tableData = this.netData.filter(e => e.nav == a)
        }
    },
    methods: {
        changenav(index, val) {
            this.nav.forEach(e => e.style = 'li')
            this.nav[index].style = 'act'
            this.navflag = val.name
        },
        deletes(val){
            this.$emit('abc',val)
        },
        takedowns(val){
            this.$emit('aaa',val)
        },
    }
}
</script>
<style lang="scss" scoped>
* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

.wrap {
    width: 100%;
    padding: 0 !important;

    .nav {
        width: 100%;
        border-bottom: 1px solid #ddd;
        display: flex;
        font-size: 14px;

        .li {
            padding: 20px 15px;
        }

        .act {
            padding: 20px 15px;
            border-bottom: 2px solid #fa4e21;
            color: #fa4e21;
        }
    }

    .table {
        margin-top: 30px;
    }

    .footer {
        margin-top: 50px;
    }
}
</style>