<template>
    <div class="wrap">
        <div class="search1">
            <div class="left">
                <div class="nav">
                    <el-button v-for="(item, index) in nav" :key="indxe" :type="item.type" round
                        @click="changenav(index)">{{ item.name }}
                    </el-button>
                </div>
                <div class="p">
                    <span>广告类型</span>
                    <el-select v-model="selval1" placeholder="请选择广告类型">
                        <el-option label="广告类型1" value="广告类型1"></el-option>
                        <el-option label="广告类型2" value="广告类型1"></el-option>
                        <el-option label="广告类型3" value="广告类型1"></el-option>
                        <el-option label="广告类型4" value="广告类型1"></el-option>
                    </el-select>
                </div>
                <div class="p">
                    <span>投放时间</span>
                    <div class="block">
                        <el-date-picker v-model="value1" type="daterange" range-separator="-" start-placeholder="开始时间"
                            end-placeholder="结束时间">
                        </el-date-picker>
                    </div>
                </div>
            </div>
            <div class="right">
                <el-button type="primary" v-if="pushcate == '0'" @click="dialogVisible = true">发布视频</el-button>
                <el-button type="primary" v-else @click="dialogVisible1 = true">发布照片</el-button>
            </div>

        </div>
        <div class="content" v-if="pushcate == '0'">
            <Con1 @abc="deletes" @aaa="takedowns"/>
        </div>
        <div class="content" v-else>
            <Con2 @abc="deletes" @aaa="takedowns" />
        </div>
        <!-- 发布视频广告 -->
        <el-dialog title="发布视频广告" :visible.sync="dialogVisible" width="50%" lock-scroll=false
            :before-close="handleClose" align="left">
            <div class="kung">
                <Pushvideo />
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
            </span>
        </el-dialog>
        <!-- 发布图片广告 -->
        <el-dialog title="发布图片广告" :visible.sync="dialogVisible1" width="50%" lock-scroll=false
            :before-close="handleClose" align="left">
            <div class="kung">
                <pushpicture />
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible1 = false">取 消</el-button>
                <el-button type="primary" @click="dialogVisible1 = false">确 定</el-button>
            </span>
        </el-dialog>
        <!-- 删除提示 -->
        <el-dialog title="提示" :visible.sync="dialogVisible2" width="25%" lock-scroll=false :before-close="handleClose"
            align="left">
            <div class="kuang">
                <Deletetag />
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible2 = false">取 消</el-button>
                <el-button type="primary" @click="dialogVisible2 = false">确 定</el-button>
            </span>
        </el-dialog>
        <!-- 下架提示 -->
        <el-dialog title="提示" :visible.sync="dialogVisible3" width="25%" lock-scroll=false :before-close="handleClose"
            align="left">
            <div class="kuang">
                <Takedown />
            </div>
        </el-dialog>
    </div>
</template>

<script>
import Con1 from './components/content1'
import Con2 from './components/content2'
import Pushvideo from './components/pushvideo'
import pushpicture from './components/pushpicture'
import Deletetag from '@/components/deletetag'
import Takedown from '@/components/Takedown'


export default {
    data() {
        return {
            nav: [
                {
                    name: "视频广告",
                    type: "primary",
                },
                {
                    name: "图片广告",
                    type: "default",

                }

            ],
            selval1: '',
            pushcate: "0",
            dialogVisible: false,
            dialogVisible1: false,
            dialogVisible2: false,
            dialogVisible3: false
        }
    },
    watch: {

    },
    methods: {
        changenav(index) {
            this.pushcate = index
            this.nav.forEach(e => e.type = 'default')
            this.nav[index].type = 'primary'
        },
        deletes(val) {
            this.dialogVisible2 = true
        },
        takedowns(val){
            this.dialogVisible3 = true 
        }

    },
    components: {
        Con1, Con2, pushpicture, Pushvideo, Deletetag, Takedown
    }
}
</script>
<style lang="scss" scoped>
* {
    box-sizing: border-box;
}

.wrap {
    width: 100%;
    height: 100%;
    padding: 20px;
    display: flex;
    flex-direction: column;

    .search1 {
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;

        .left {
            display: flex;
            align-items: center;

            .nav {
                margin-right: 30px;
            }

            .p {
                display: flex;
                align-items: center;
                margin-right: 30px;

                span {
                    width: 80px;
                }
            }

        }
    }

    .content {
        width: 100%;
        margin-top: 20px;
    }

    .kung {
        width: 100%;
        height: 500px;
        overflow: auto;
        padding: 0;
    }

    .kuang {
        width: 100%;
        padding: 0;
    }

}
</style>
